/* page header*/

header {
    display: flex;
    position: absolute;
    height: 15%;
    min-height: 75px;
    max-height: 125px;
    width: 95%;
    max-width: 2000px;
    min-width: 250px; 
    background-color: #1b1b1b;
    box-shadow: 0.5rem 0.5rem 0.5rem #0a0a0a;
    color: #ffffff;
    left: 50%;
    top: 0;
    transform: translate(-50%,0%);
    z-index: 3;
}

nav.upheader {
    height: 75%;
    width: 100%;
    display: inline-flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-content: space-around;
    justify-content: space-between;
    align-items: center;
}

nav.upheader span {
    display: inline-flex;
}

a.logo {
    font-family: 'Frijole';
    color: #69d300;
    font-size: 25px;
    margin-left: 25px;
    transition: 1000ms;
    text-decoration: none;
}

@media only screen and (max-width: 1200px) {
    nav.upheader span {
        font-size: 20px;
    }
}

a.login {
    font-family: 'Nunito';
    text-decoration: none;
    font-size: 20px;
    color: #ffffff;
    transition: 1000ms;
    margin-right: 10px;
}

@media only screen and (max-width: 1200px) {
    nav.upheader span a {
        font-size: 15px;
    }
}

ul {
    background-color: #202020;
    list-style-type: none;
    display: inline-flex;
    overflow: hidden;
    position: absolute;
    width: 100%;
    left: 50%;
    margin: 0;
    bottom: 0;
    transform: translate(-50%,0%);
}

ul li {
    position: relative;
    width: 65px;
    height: 30px;
    display: inline-flex;
    transition: 400ms;
    align-items: center;
}

@media only screen and (max-width: 1200px) {
    ul li {
        height: 20px;
    }
}

ul li:hover {
    cursor: pointer;
    background-color: rgb(36, 35, 35);
}

ul li a {
    padding: 10px 0 10px 0;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif ;
    transition: 1000ms;
}

@media only screen and (max-width: 1200px) {
    ul li a{
        font-size: 12px;
    }
}